import {
  Box,
  Typography,
  Divider,
  Grid,
  InputAdornment,
  Slide,
} from "@mui/material";
import Grid2 from "@mui/material/Unstable_Grid2";
import { useEffect, useState } from "react";
import { CustomButton } from "../component/buttons";
import { CustomInput } from "../component/InputField";
import LockOpenSharpIcon from "@mui/icons-material/LockOpenSharp";
import { useBearStore } from "../store";

export default function GetStarted() {
  const [submitState, setSubmitState] = useState<any>("");
  const [phone, setPhone] = useState<any>("");
  const [phoneError, setPhoneError] = useState<any>("");
  const [button, setButton] = useState<any>(true);
  const [code, setCode] = useState<any>(true);

  const setPage = useBearStore((state:any) => state.setPage)
  const page = useBearStore((state:any) => state.page)
  function handlePhone(e: any) {
    let phone = e.target.value;
    setPhone(e.target.value);
  }
  function handleCode(e:any) {
    setCode(e.target.value)
  }

  function handleSubmit() {
    if(submitState===""){
      fetch("/check",{
        headers:{
          'Content-Type': 'application/json'
        },
        method:"POST",
        body:JSON.stringify({phone})
      }).then(ress=>ress.json())
        .then(response=>{
          console.log(response.message)
          setSubmitState(response.message)          
        })
    }else if(submitState==="code"){
      fetch("/verify",{
        headers:{
          'Content-Type': 'application/json'
        },
        method:"POST",
        body:JSON.stringify({phone,token:code})
      }).then(ress=>ress.json())
        .then(response=>{
          if(response.message === "register"){
            setPage(response.message)          
            console.log(page)
          }else{
            alert(response.message)
          }
        })
    }
  }
  useEffect(() => {
    if (phone.length) {
      if (phone.length === 10) {
        setPhoneError("");
        setButton(false)
      }else{
        setPhoneError("invalid phone");
        setButton(true)

    }
  }}, [phone]);

  return (
    <Grid2>
      <Typography
        gutterBottom
        variant="h5"
        component="div"
        textAlign={"center"}
      >
        Get Started
      </Typography>
      <Divider
        sx={{
          mb: 5,
        }}
        variant="middle"
      />
      <Grid gap={5} container>
        <Typography textAlign={"center"} mt={2} fontWeight={200}>
          Enter A valid Phone Number
        </Typography>
        <CustomInput
          variant="outlined"
          label="Phone"
          size="small"
          type="number"
          helperText={phoneError}
          error={phoneError&&true}
          onChange={handlePhone}
          fullWidth
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">+234</InputAdornment>
            ),
          }}
        />
        {submitState === "code" && (
          <Slide direction="up" in={submitState=== "code"} mountOnEnter unmountOnExit>
            <Grid>
              <CustomInput
                variant="outlined"
                label="Verification code"
                helperText={`enter code sent to your number`}
                onChange={handleCode}
                size="small"
              />
              <CustomButton disabled sx={{ display: "inline", ml: 1 }}>
                Resend
              </CustomButton>
            </Grid>
          </Slide>
        )}
        {submitState === "password" && (
          <CustomInput
            variant="outlined"
            label="Password"
            type={"password"}
            fullWidth
            size="small"
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <LockOpenSharpIcon />
                </InputAdornment>
              ),
            }}
          />
        )}
        <CustomButton
          onClick={handleSubmit}
          disabled={button}
          disableFocusRipple
          disableRipple
          fullWidth
        >
          Next
        </CustomButton>
      </Grid>
    </Grid2>
  );
}
