import {
  Box,
  Grid,
  Typography,
  Divider,
  InputAdornment,
  IconButton,
  CircularProgress,
  FormControl,
  InputLabel,
  MenuItem,
  Select,
  TextareaAutosize,
  Button,
} from "@mui/material";
import Grid2 from "@mui/material/Unstable_Grid2";
import React, { useEffect, useState } from "react";
import GetStarted from "../../onBoarding/GetStarted";
import AddAPhotoIcon from "@mui/icons-material/AddAPhoto";
import { CustomInput } from "../InputField";
import CheckIcon from "@mui/icons-material/Check";
import { stateList } from "../../assets/stateList";
import { CustomButton } from "../buttons";

function MainForm() {
  const [fileDataURL, setFileDataURL] = useState("");
  const [isNIN, setIsNIN] = useState("")
  const [ninNumber, setninNumber] = useState("")

  function handleNIN(e:any) {
    setninNumber(e.target.value)
  }
  useEffect(() => {
  if (ninNumber.length === 10) {
    setIsNIN("loading")
  }else{
    setIsNIN("invalid NIN")
  }
  }, [ninNumber])
  

  const imageMimeType = /image\/(png|jpg|jpeg)/i;
  const changeHandler = (e:any) => {
    const file = e.target.files[0];
    if (!file.type.match(imageMimeType)) {
      alert("Image mime type is not valid");
      return;
    }
    const objectUrl = URL.createObjectURL(file)
    setFileDataURL(objectUrl);
  }
  return (
    <Box
      sx={{
        padding: 5,
        height: "100vh",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        backgroundColor: "green",
      }}
    >
      <Grid2
        p={5}
        flexDirection={"row"}
        flexWrap={"nowrap"}
        gap={2}
        container
        borderRadius={3}
        boxShadow={"red"}
        bgcolor={"#ffff"}
        height={550}
        width={700}
      >
        <Grid>
          <Grid
            container
            justifyContent={"center"}
            alignItems={"center"}
            
            sx={{
              bgcolor: "#ccccac9b",
              border: "1px solid #00000014",
              borderRadius: "5px",
              cursor: "pointer",
              width: 140,
              height: 150,
            }}
          >
            {fileDataURL?<img width={140} height={150} src={fileDataURL} alt="avater" />:<IconButton
              color="primary"
              aria-label="upload picture"
              component="label"
            >
              <input  onChange={changeHandler} hidden accept="image/*" type="file" />
              <AddAPhotoIcon />
            </IconButton>} 
          </Grid>
          <Typography color={"grey"} fontSize={11} >Supported fomat jpeg/png/jpg</Typography>
          <Typography mb={3} fontSize={11}>max size 2mb</Typography>
          {/* <CustomButton color="secondary" variant="text">Delete</CustomButton> */}
          <CustomInput
            variant="outlined"
            label="NIN"
            size="small"
            type="tel"
            onChange={handleNIN}
            fullWidth
            error={isNIN==="invalid NIN"?true:false}
            InputProps={{
              endAdornment:(isNIN==="ok"?<CheckIcon color="success" />:isNIN==="loading"?<CircularProgress size={20} color="success" />:""),
            }}
          />
          
        </Grid>
        <Divider orientation="vertical" variant="middle" flexItem />
        <Grid2 width={"100%"}>
          <Typography
            mb={3}
            fontWeight={700}
            fontSize={21}
            textAlign={"center"}
          >
            National Directorate of Employment
          </Typography>
          <Grid>
            <Typography>Address</Typography>
            <Grid p={2} gap={1} container>
              <Grid xs={5.5}>
                <FormControl fullWidth size="small">
                  <InputLabel id="demo-select-small">State</InputLabel>
                  <Select
                    fullWidth
                    labelId="demo-select-small"
                    id="demo-select-small"
                    label="Age"
                    size="small"
                  >
                    <MenuItem value="">
                      <em>None</em>
                    </MenuItem>
                    {stateList.map(state=>(
                      <MenuItem value={state.state}>{state.state}</MenuItem>
                    ))}
                  </Select>
                </FormControl>
              </Grid>
              <Grid xs={5.5}>
                <FormControl fullWidth size="small">
                  <InputLabel id="demo-select-small">LGA</InputLabel>
                  <Select
                    fullWidth
                    labelId="demo-select-small"
                    id="demo-select-small"
                    label="Age"
                    size="small"
                  >
                    <MenuItem value="">
                      <em>None</em>
                    </MenuItem>
                    <MenuItem value={10}>Ten</MenuItem>
                    <MenuItem value={20}>Twenty</MenuItem>
                    <MenuItem value={30}>Thirty</MenuItem>
                  </Select>
                </FormControl>
              </Grid>
              <Grid xs={11.25} >
                <CustomInput label="Address" minRows={3} multiline size="small" fullWidth />
              </Grid>
            </Grid>
          </Grid>
          <Grid>
            <Typography>Qualification</Typography>
            <Grid p={2} gap={1} container>
              {/* honor */}
              <Grid xs={5}>
                <FormControl fullWidth size="small">
                  <InputLabel id="demo-select-small">Honour</InputLabel>
                  <Select
                    fullWidth
                    labelId="demo-select-small"
                    id="demo-select-small"
                    label="Age"
                    size="small"
                  >
                    <MenuItem value="">
                      <em>None</em>
                    </MenuItem>
                    <MenuItem value={10}>Degree</MenuItem>
                    <MenuItem value={20}>HND</MenuItem>
                    <MenuItem value={30}>ND</MenuItem>
                    <MenuItem value={30}>NCE</MenuItem>
                  </Select>
                </FormControl>
              </Grid>
              {/* course */}
              <Grid xs={6.5}>
                <FormControl fullWidth size="small">
                  <InputLabel id="demo-select-small">Course</InputLabel>
                  <Select
                    fullWidth
                    labelId="demo-select-small"
                    id="demo-select-small"
                    label="Age"
                    size="small"
                  >
                    <MenuItem value="">
                      <em>None</em>
                    </MenuItem>
                    <MenuItem value={10}>Biology</MenuItem>
                    <MenuItem value={20}>Mathematics</MenuItem>
                    <MenuItem value={30}>litreture</MenuItem>
                    <MenuItem value={30}>Agric</MenuItem>
                  </Select>
                </FormControl>
              </Grid>
              {/* institute */}
              <Grid container gap={1}>
                <Grid xs={12}>
                  <CustomInput size="small" label="Institution" fullWidth />
                </Grid>
                <Grid container gap={1}>
                  <CustomInput size="small" label="Year" type="month" />
                  <Button size="small" variant="contained" component="label">
                    Upload
                    <input hidden accept="image/*" multiple type="file" />
                  </Button>
                </Grid>
              </Grid>
            </Grid>
          <Grid mt={1} xs={11} >
            <CustomButton fullWidth >Submit</CustomButton>
          </Grid>
          </Grid>
        </Grid2>
      </Grid2>
    </Box>
  );
}

export default MainForm;
