import { Typography, Divider, Grid, FormControl, FormLabel, RadioGroup, FormControlLabel, Radio } from "@mui/material";
import Grid2 from "@mui/material/Unstable_Grid2";
import { useState } from "react";
import { CustomButton } from "../component/buttons";
import { CustomInput } from "../component/InputField";
import { useBearStore } from "../store";


export default function Register() {
    const [registered, setRegistered] = useState(false);
    const setForms = useBearStore((state:any) => state.setForms)
    function handleRegister() {
      setForms() 
      fetch("/check",{
        headers:{
          'Content-Type': 'application/json'
        },
        method:"POST",
        body:JSON.stringify({})
      }).then(ress=>ress.json())
        .then(response=>{
          console.log(response.message)
          setForms(response.message)          
        })
    }
    return ( 
        <Grid>
        <Typography
          gutterBottom
          mb={2}
          variant="h5"
          component="div"
          textAlign={"center"}
        >
          Create Account
        </Typography>
        <Divider
          sx={{
            mb: 3,
          }}
          variant="middle"
        />
        <Grid container gap={4} flexDirection={"column"} flexWrap={"nowrap"}>
          <Grid flexWrap={"nowrap"} gap={1} container>
            <Grid item sm={6} xs={12}>
              <CustomInput
                variant="outlined"
                label="Last Name"
                size="small"
                type="text"
                fullWidth
              />
            </Grid>
            <Grid item sm={6} xs={12}>
              <CustomInput
                variant="outlined"
                label="First Name"
                size="small"
                type="text"
                fullWidth
              />
            </Grid>
            {/* <Grid item sm={4} xs={12} >
          <TextField
            label="Middle Name"
            variant="filled"
            size="small"
            type="text"
            fullWidth
          />
          </Grid> */}
          </Grid>
          <Grid flexWrap={"nowrap"} gap={2} container>
            <Grid item sm={6} xs={12}>
              <FormControl>
                <FormLabel id="demo-row-radio-buttons-group-label">
                  Date of Birth
                </FormLabel>
                <CustomInput variant="outlined" size="small" type="date" />
              </FormControl>
            </Grid>
            <Grid item sm={6} xs={12}>
              <FormControl>
                <FormLabel id="demo-row-radio-buttons-group-label">
                  Gender
                </FormLabel>
                <RadioGroup
                  sx={{
                    display: "flex",
                    flexWrap: "noWrap",
                  }}
                  row
                  aria-labelledby="demo-row-radio-buttons-group-label"
                  name="row-radio-buttons-group"
                >
                  <FormControlLabel
                    value="female"
                    control={<Radio size="small" />}
                    label="Female"
                  />
                  <FormControlLabel
                    value="male"
                    control={<Radio size="small" />}
                    label="Male"
                  />
                </RadioGroup>
              </FormControl>
            </Grid>
          </Grid>
          <Grid container gap={3}>
            <CustomInput
              variant="outlined"
              size="small"
              fullWidth
              label="Email"
              type="email"
            />
            <Grid container flexWrap={"nowrap"} gap={1}>
              <Grid item sm={6} xs={12}>
                <CustomInput
                  variant="outlined"
                  size="small"
                  label="Password"
                  type="password"
                />
              </Grid>
              <Grid item sm={6} xs={12}>
                <CustomInput
                  variant="outlined"
                  size="small"
                  label="Confrim Password"
                  type="password"
                />
              </Grid>
            </Grid>
          </Grid>
  
          <CustomButton
            onClick={handleRegister}
            disableFocusRipple
            disableRipple
            fullWidth
          >
            Register
          </CustomButton>
        </Grid>
        </Grid>
        
    );
  }